<template>
	<div>
		<div class="login-title">
			<h3>账号登录</h3>
			<p>没有账号？
				<router-link class="lt-register" tag="span" to="register">立即注册</router-link>
			</p>
		</div>
		<div class="lr-content">
			<el-form :model='user' :rules='rules' ref='login-form' @keyup.enter.native="login">
				<el-form-item prop="userName">
					<el-input v-model="user.userName" placeholder="账号" />
				</el-form-item>
				<el-form-item prop="userPassword">
					<el-input type="password" v-model="user.userPassword" placeholder="密码" />
				</el-form-item>
				<el-form-item>
					<el-button class="lr_btn" @click='login'>立即登录</el-button>
				</el-form-item>
			</el-form>
		</div>
	</div>
</template>

<script>
	import {
		userLogin
	} from "@/network/user";
	export default {
		name: 'login',
		data() {
			return {
				user: {
					userName: '',
					userPassword: ''
				},
				rules: {
					userName: {
						required: true,
						message: '请输入账号',
						trigger: 'blur'
					},
					userPassword: {
						required: true,
						message: '请输入密码',
						trigger: 'blur'
					}
				}
			}
		},
		methods: {
			login() {
				userLogin(this.user.userName, this.user.userPassword).then(
					res => {
						if (res.code === 1) {
							this.$store.dispatch('user/saveUserInfo').then(() => {
								this.msg(res.msg, 'success')
								this.$router.replace('/home')
							})
						} else {
							this.msg(res.msg, 'error')
						}
					});
			},
			msg(msg, type) {
				this.$message({
					showClose: true,
					message: msg,
					duration: 1000,
					type: type
				});
			}
		}
	}
</script>

<style>
	.lt-register {
		color: #008489 !important;
		cursor: pointer;
	}

	.lt-register:hover {
		padding-bottom: 1px;
		border-bottom: 1px solid #008489;
	}
</style>
